<template>
<header class="header">
	<div class="hd flex-wrap">
		<div id="logo">
			<a href="">
				<img :src="logo" alt="">
			</a>
		</div>
		<div class="hd_inner flex-wrap flex-con">
			<a class="ui_btn" v-link="{ name: 'publish'}">我要发布</a>
			<div class="school_box">
				<ui-select-box :data="schoolList"></ui-select-box>
			</div>
			<div class="search_box">
				<input type="text" v-model="searchKey">
				<a class="btn_search" v-link="{ path: '/search/' + searchKey }"></a>
			</div>
		</div>
		<div class="auth_box flex-wrap" v-if="!isLogin">
			<a class="ui_btn" v-link="{ name: 'login' }">登录</a>
			<a class="ui_btn ui_btn_default" v-link="{ name: 'register' }">注册</a>
		</div>
		<div class="auth_box flex-wrap" v-if="isLogin">
			<a class="ui_btn" v-link="{ name: 'user' }">{{userName}}</a>
			<a class="ui_btn" v-link="{ name: 'home' }" @click="unLogin">退出登录</a>
		</div>
	</div>
</header>
</template>

<script>
import uiSelectBox from './selectbox'
import logo from '../assets/img/logo.png'

export default {
	data() {
		return {
			logo: logo,
			searchKey: 'iphone',
			schoolList: [],
			isLogin: false,
			userName: ''
		}
	},
	components: {
		uiSelectBox
	},
	ready() {
		const isLogin = localStorage.getItem('userLogin');
		if(isLogin) {
			this.isLogin = isLogin;
			this.userName = JSON.parse(localStorage.getItem('userInfo')).username;
		}
        this.getSchoolList();
    },
	methods: {
		getSchoolList() {
			const schoolLists = require('../../mock/schoollists');
			this.schoolList = schoolLists.default.data;
		},
		unLogin() {
			localStorage.removeItem("userLogin");
			localStorage.removeItem("userInfo");
			this.isLogin = false;
			this.$router.beforeEach((transition) => {
				transition.next();
			});
		}
	}
}

</script>

<style lang="less">
@baseHeight: 30px;

.header{
	background-color: #fff;

	.hd{
		width: 1000px;
		margin: 0 auto;
		.ui_btn{
			cursor: pointer
		}
		#logo{
			width: 90px;
			height: 42px;
			margin: 30px 40px 30px 0;
			img{
				width: 100%;
			}
		}

		&_inner{
			-webkit-justify-content: space-between;
    		-webkit-align-items: center;
			justify-content: space-between;
			align-items: center;
			margin-right: 30px;

			.school_box{
				width: 140px;
			}

			.search_box{
				position: relative;
				border: 1px solid #c8c8c8;
				padding-right: 30px;
				input{
					width: 350px;
					height: @baseHeight;
					padding: 0 10px;
					border:0;
					-webkit-box-sizing: border-box;
					box-sizing: border-box;
				}
				
				.btn_search{
					position: absolute;
					top: 0;
					right: 0;
					width: @baseHeight;
					height: @baseHeight;
					background: url(../assets/img/btn-search.png) no-repeat center center;
					background-size: 50%;
					cursor: pointer;
				}

			}
		}

		.auth_box{
			width: 197px;
			-webkit-justify-content: space-between;
    		-webkit-align-items: center;
			justify-content: space-between;
			align-items: center;
		}
	}
}
</style>


