<style lang="less">
@imgWidth: 175px;
@baseColor: #c8c8c8;
.ui_select .select_data{
	width: 120px !important;
}
.login{
	display: flex;
	justify-content: center;
	height: 500px;
	align-items: center;
}
.publish_wrap {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: center;
	.pubish {
		margin: 0;
		width: 1070px;
		.icon {
			display: inline-block;
			height: 15px;
			width: 15px;
			overflow: hidden;
			margin-right: 10px;
		}
		.icon_title{
			margin-right: 50px;
		}
		.title {
			font-size: 28px;
		    height: 100px;
		    line-height: 100px;
		    text-align: center;
		    font-weight: initial;
		}
		.aside{
			border-bottom: 1px solid @baseColor;
			padding: 35px 0;
		}
		.addPic{
			display: flex;
			flex-direction: row;
			justify-content: center;
			.new_imgs {
				display: flex;
				flex-direction: row;
				li {
					position: relative;
					width: @imgWidth;
					height: @imgWidth;
					margin-right: 15px;
					&:hover .mask{
						background: rgba(0, 0, 0, .7);
					}
					&:hover .remove{
						opacity: 1;
					}
					a {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
					}
					.remove {
						opacity: 0;
						display: flex;
						justify-content: center;
						align-items: center;
						font-size: 80px;
						color: #ff6e52;
						transform:rotate(45deg);
					}
				}
			}
			.add_btn{
				width: @imgWidth;
				height: @imgWidth;
				border: 1px solid @baseColor;
				a{
					display: flex;
					justify-content: center;
					align-items: center;
					width: 100%;
					height: 100%;
					font-size: 100px;
					font-weight: lighter;
					color: #b7b7b7;
				}
			}
		}
		.cate_type{
			display: flex;
			flex-direction: row;
			justify-content: center;
			.select_cate_type{
				display: flex;
				flex-direction: column;
				margin-right: 195px;
				.cate {
					display: flex;
    				flex-direction: row;
    				align-items: center;
    				margin-bottom: 35px;
					.cate_icon {
						background: url('../../assets/img/publishsprite.png') -93px -0px no-repeat;
					}
				}
				.type {
					display: flex;
					flex-direction: row;
					.type_icon {
						background: url('../../assets/img/publishsprite.png') -61px -0px no-repeat;
						margin-top: 10px;
					}
					.ct_title{
						margin-top: 10px;
					}
					.linkage{
						.first {
							margin-bottom: 20px;
						}
					}
				}
			}
			.select_business {
				width: 285px;
				color: #666;
				div{
					margin-bottom: 10px;
					display: flex;
					height: 35px;
					align-items: center;
					span {
						flex: 1;
					}
					a{
						width: 85px;
						height: 100%;
						text-align: center;
						line-height: 35px;
						color: #666;
					}
					.select_btn {
						background: #ffc124;
					}
				}

			}
		}
		.details {
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-left: 160px;
			.details_title {
				margin-bottom: 25px;
				.title_icon {
					background: url('../../assets/img/publishsprite.png') -109px -0px no-repeat;
				}
				.title_title{
					margin-right: 72px;
				}
				input{
					width: 545px;
					height: 30px;
				}
			}
			.details_info {
				margin-bottom: 25px;
				.info_icon{
					background: url('../../assets/img/publishsprite.png') -77px -0px no-repeat;
				}
				span {
					vertical-align: top;
				}
				.title_info{
					margin-right: 72px;
				}
				textarea{
					width: 545px;
					height: 170px;
				}
			}
			.bussiness_address {
				margin-bottom: 25px;
				.adress_icon {
					background: url('../../assets/img/publishsprite.png') -0px -0px no-repeat;;
				}
				.title_adress{
					margin-right: 45px;
				}
				.inschool{
					margin-right: 118px;
				}
				input{
					width: 210px;
					height: 30px;
				}
			}
			.expect_price {
				margin-bottom: 25px;
				.price_icon {
					background: url('../../assets/img/publishsprite.png') -33px -0px no-repeat;
				}
				.title_price{
					margin-right: 45px;
				}
				.exchangegood_icon{
					background: url('../../assets/img/publishsprite.png') -14px -0px no-repeat;
				}
				.price{
					width: 105px;
    				margin-right: 54px;
				}
				input{
					width: 210px;
					height: 30px;
				}
			}
		}
		.phone{
			border-bottom: 0;
			padding-left: 160px;
			.phone_title{
				margin-bottom: 40px;
				.phone_icon{
					background: url('../../assets/img/publishsprite.png') -45px -0px no-repeat;
				}
			}
			.text_wrap{
				margin-left: 60px;
				margin-bottom: 50px;
			    span{
			    	margin-right: 50px;
			    }
				.qq{
					margin-right: 100px;
				}
			}
			input{
					width: 210px;
					height: 30px;
				}
		}
		.save{
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 90px;
			a {
				width: 115px;
				height: 30px;
				background: #ffc124;
				text-align: center;
				line-height: 30px;
				color: #fff;
			}
		}
	}
	.official_about{
		right: 30px;
		bottom: 90px;
	}
}
</style>
<template>
	<ui-header></ui-header>
	<div v-if="!isLogin" class="login">请先登录...</div>
	<div v-if="isLogin" class="publish_wrap">
		<div class="pubish">
			<h1 class="title">
				发布信息
			</h1>
			<div class="aside addPic">
	            <ul v-if="images.length >0" class="new_imgs">
	                <li v-for="(key,image) in images">
	                    <img :src="image" width="100%" height="100%" />
	                    <a class="mask"></a>
	                    <a href="" class="remove" v-on:click.prevent="delImage(key)">+</a>
	                </li>
	            </ul>
	            <div class="add_btn">
	                <a href="" v-on:click.prevent="addPic">+</a>
	                <input type="file" @change="onFileChange" multiple style="display: none;">
	            </div>
			</div>
			<div class="aside cate_type">
				<div class="select_cate_type">
					<div class="cate">
						<span class="icon cate_icon"></span>
						<span class="ct_title icon_title">信息类别</span>
						<ui-Type-Select-Box :data="typeList" @typeid="getTypeId"></ui-Type-Select-Box>
					</div>
					<div class="type">
						<span class="icon type_icon"></span>
						<span class="ct_title icon_title">物品分类</span>
						<div class="linkage">
							<div class="first">
								<ui-First-Cate-Select :data="firstgoodCate" @firstcategoryid="getFiratCateId"></ui-First-Cate-Select>
							</div>
							<ui-Sec-Cate-Select :data="curSecCate" :focus="secFocus" @seccategoryid="getSecCateId"></ui-Sec-Cate-Select>
						</div>
					</div>
				</div>
				<div class="select_business">
					<div class="isbargain">
						<span>允许议价？</span>
						<a :class="[isBargain ? 'select_btn' : '']" href="#" @click.prevent="this.isBargain=true">是</a>
						<a :class="[isBargain ? '': 'select_btn']" href="#" @click.prevent="this.isBargain=false">否</a>
					</div>
					<div class="isbusiness">
						<span>允许校外交易？</span>
						<a :class="[isBussiness ? 'select_btn' : '']" href="#" @click.prevent="this.isBussiness=true">是</a>
						<a :class="[isBussiness ? '': 'select_btn']" href="#" @click.prevent="this.isBussiness=false">否</a>
					</div>
					<div class="isexchange">
						<span>允许以物换物？</span>
						<a :class="[isExchange ? 'select_btn' : '']" href="#" @click.prevent="this.isExchange=true">是</a>
						<a :class="[isExchange ? '': 'select_btn']" href="#" @click.prevent="this.isExchange=false">否</a>
					</div>
				</div>
			</div>
			<div class="aside details">
				<div class="details_title">
					<span class="icon title_icon"></span>
					<span class="title_title icon_title">标题</span>
					<input type="text" placeholder="不超过30个字" id="title"/>
				</div>
				<div class="details_info">
					<span class="icon info_icon"></span>
					<span class="title_info icon_title">详情</span>
					<textarea placeholder='购买时间；耗损程度；购买价格；包装，说明书，发票是否齐全；用途；其他' id="information"></textarea>
				</div>
				<div class="bussiness_address">
					<span class="icon adress_icon"></span>
					<span class="title_adress icon_title">交易地点</span>
					<input type="text" placeholder="校内地点" class="inschool" id="inschool"/>
					<input type="text" placeholder="校外地点（范围）" id="outschool"/>
				</div>
				<div class="expect_price">
					<span class="icon price_icon"></span>
					<span class="title_price icon_title">期望价格</span>
					<input type="text" placeholder="￥" class="price" id="price"/>
					<span class="icon exchangegood_icon"></span>
					<span class="title_exchangegood icon_title">期望置换物品</span>
					<input type="text"id="exchangegood" />
				</div>
			</div>
			<div class="aside phone">
				<div class="phone_title">
					<span class="icon phone_icon"></span>
					<span class="title_phone icon_title">联系方式</span>
				</div>
				<div class="text_wrap">
					<span>QQ</span>
					<input type="text" class="qq" id="qq"/>
					<span>手机</span>
					<input type="text" id="phone"/>
				</div>
			</div>
			<div class="save">
				<a @click.prevent='uploadImage' href="">保存</a>
			</div>
		</div>
		<ui-Official></ui-Official>
	</div>
	<ui-footer></ui-footer>
</template>

<script>
import uiHeader from '../../components/header'
import uiFooter from '../../components/footer'
import uiOfficial from '../../components/official'
import uiTypeSelectBox from './typeselectbox'
import uiFirstCateSelect from './firstcateselect'
import uiSecCateSelect from './seccateselect'

//父组件
export default {
	data() {
		return {
			isLogin: false,
			images: [], //上传的图片
			typeList: [], //信息类型列表
			firstgoodCate: [], //一级分类
			curSecCate: [], //当前二级分类，联动的
			secgoodCate: [], //二级分类表
			typeId: -1, //当前所选信息分类id
			firstCateId: -1, //当前所选一级分类id
			secCateId: -1, // 当前所选二级分类id
			secFocus: {
				"informationname": "请选择",
				"parentId": -2
			},
			isBargain: true,
			isBussiness: true,
			isExchange: true
		}
	},
    ready() {
    	const isLogin = localStorage.getItem('userLogin');
		if(isLogin) {
			this.isLogin = isLogin;
			this.fetchGoodsCategories();
		}
    },
	methods: {
			fetchGoodsCategories() {
				setTimeout(() => {
	                let categories = require('../../../mock/categories'); //请求
	                categories = categories.default;
	                const firstCates = categories.firstCategories.data,
	                	secondCates = categories.secondCategories.data,
	                	infoTypes = categories.infoTypes.data;
	                this.firstgoodCate = firstCates;
	                this.secgoodCate = secondCates;
	                this.curSecCate = secondCates[0].childrenCates;
	                this.typeList = infoTypes;
	            }, 1000);
			},
			getTypeId(id) {
				this.typeId = id;
			},
			getFiratCateId(id) {
				this.firstCateId = id;
				this.secFocus = {  //重新赋值
					"informationname": "请选择",
					"parentId": -2
				};
				this.secgoodCate.forEach((cate, idx) => {
					if (cate.parentId === id) {
						this.curSecCate = cate.childrenCates;
					}
				});
			},
			getSecCateId(id) {
				this.secCateId = id;
			},
            addPic(e){
                document.querySelector('input[type=file]').click();
                return false;
            },
            onFileChange(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)return; 
                this.createImage(files);

            },
            createImage(file) {
                if(typeof FileReader==='undefined'){
                    alert('您的浏览器不支持图片上传，请升级您的浏览器');
                    return false;
                }
                var image = new Image();         
                var vm = this;
                var leng=file.length;
                for(var i=0;i<leng;i++){
                    var reader = new FileReader();
                    reader.readAsDataURL(file[i]); 
                    reader.onload =function(e){
                    vm.images.push(e.target.result);                                    
                    };                 
                }                        
            },
            delImage:function(index){
                this.images.shift(index);
            },
            uploadImage: function() {
            	//检测
            	if(!(this.firstCateId > 0 && this.secCateId > 0)) {
					alert('请选择物品分类');
					return;
            	}
            	const title = document.querySelector('#title').value;
            	if(title && title.length > 30) {
					alert('标题名称不能超过30字');
					return;
            	}else if(!title){
            		alert('请填写标题');
            		return;
            	}
            	const inschool = document.querySelector('#inschool').value,
            		outschool = document.querySelector('#outschool').value;
            	if(inschool=='' && outschool=='') {
					alert('请填写交易地点');
					return;
            	}
            	const exprice = document.querySelector('#price').value;
            	if(!exprice) {
					alert('请填写期望价格');
					return;
            	}
            	const exchangegood = document.querySelector('#exchangegood').value;
            	if(this.isExchange && exchangegood=='') {
					alert('请填写期望置换物品');
					return;
            	}
            	const restelephone = document.querySelector('#phone').value;
            	if(!restelephone) {
					alert('请填写手机');
					return;
            	}else if(restelephone.search(/\d{11}/)!==0){
					alert('请填写正确的手机号码');
					return;
            	}
                const obj = {};
                obj.infoimgs=this.images;
                obj.informationtypeid = this.typeId;
                obj.informationcategoryid = this.secCateId;
                obj.isbargain = this.isBargain;
                obj.isbusiness = this.isBussiness;
                obj.isexchange = this.isExchange;
                obj.title = document.querySelector('#title').value;
                obj.informationdes = document.querySelector('#information').value;
                obj.address = `${inschool}#${outschool}`;
                obj.exprice = exprice;
                obj.heat = 100;
                obj.informationstatus = 0;
                obj.pictstatus = -1;
                obj.renttime = this.secCateId === 3 ? new Date().getTime() : '';
				obj.resqq = document.querySelector('#qq').value;
				obj.restelephone = document.querySelector('#phone').value;
				obj.schoolid = 9011;
				obj.substitute = this.isExchange ? exchangegood : '';
				//请求
				alert('提交成功！');
            }
        },
	components: {
		uiHeader,
		uiFooter,
		uiOfficial,
		uiTypeSelectBox,
		uiFirstCateSelect,
		uiSecCateSelect
	}
}
</script>


